<template>
  <div>
    <el-page-header @back="goBack" content="播放" class="page-header">
    </el-page-header>
    <div class="iframe-video-wrap">
      <IframeVideo :url="iframeUrl" ref="IframeVideo" class="iframe-video"></IframeVideo>
      <div class="display-item-wrap">
        <button v-for="(item,index) in displayList" :class="'display-item '+(active==index?'active':'' )"
                @click="display(item.displayUrl,index)">
          {{ item.displayName }}
        </button>
      </div>
    </div>
    <hr>
    <div
        style="box-sizing: border-box; width: 100%; padding: 0px 5px;display: flex;flex-direction: row;flex-wrap: nowrap">
      <div style="flex: 1">
        <el-select
            placeholder="请选择"
            style="width: 100%"
            v-model="parseUrl"
            @change="reload"
        >
          <el-option
              v-for="item in videoParseUrls"
              :key="item.id"
              :label="item.name"
              :value="item.url"
          >
          </el-option>
        </el-select>
      </div>
      <el-button
          icon="el-icon-refresh"
          circle
          style="margin-left: 20px"
          @click="reload"
      ></el-button>
    </div>
  </div>
</template>

<script scoped>
import IframeVideo from '@/components/IframeVideo'
import {routerUtils} from '@/utils/RouterUtils.js'
import {displayList, parseUrls} from "../../api/display";

export default {
  name: 'Display',
  components: {
    IframeVideo,
  },
  props: {
    videoName: String,
    displayUrl: String
  },
  data: function () {
    return {
      active: 0,
      parseUrl: 'https://jx.xmflv.com/?url=',
      videoParseUrls: [],
      url: '',
      displayList: []
    }
  },
  methods: {
    goBack() {
      routerUtils.push({path: '/searchList'})
    },

    reload() {
      this.$refs.IframeVideo.reloadIframe()
    },

    display(url, num) {
      this.active = num
      this.url = url
    }
  },

  computed: {
    iframeUrl() {
      let displayUrl = this.parseUrl + this.url
      console.log('displayUrl==>' + displayUrl)
      return displayUrl
    },
  },

  watch: {
    videoName() {
      console.log('watch.videoName==>' + this.videoName)
    },
    displayUrl() {
      console.log('watch.displayUrl==>' + this.displayUrl)
    },
  },

  created() {
    this.url = this.id
  },

  beforeCreate() {
    // 加载视频播放解析地址
    parseUrls().then(res => {
      let data = res.data
      this.parseUrl = data.defalutParseUrl
      this.videoParseUrls = data.videoParseUrls
    })

    // 加载播放视频详细信息
    displayList().then(res => {
      this.displayList = res.data
      if (this.displayList.length > 0) {
        this.url = this.displayList[0]['displayUrl']
      }
    })

  },

  beforeMount() {
  },

  mounted() {
  },
}
</script>

<style lang="css" scoped>
@import "../../css/common.css";

.active {
  background-color: #909399 !important;
  border-color: #909399 !important;
  color: #fff !important;
}

.display-item-wrap {
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 5px 5px;
  background-color: #e0e0e0;
  padding: 5px 0px;
  border-radius: 10px;

}

.display-item {
  width: 50px;
  height: 30px;
  font-size: 14px;
  line-height: 30px;
  margin: 5px 5px;
  border-radius: 5px;
  background-color: white;
}


.iframe-video-wrap {

}

.iframe-video{
  width: 100%;
  height: calc( 100vw * (9/16) );
}

@media screen and (min-width: 800px) {
  .iframe-video-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .iframe-video{
    width: 60% !important;
    height: calc( 60vw * (9/16) ) !important;
  }

  .display-item-wrap {
    display: flex;
    justify-content: left;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    background-color: #e0e0e0;
    padding: 5px 0px;
    border-radius: 10px;
    box-sizing: border-box;
    width: 40%;
  }
}
</style>
