<template>
  <div style="width: 100%;height: calc( 100vw * (9/16) );">
    <iframe
        style="width: 100%;height: 100%"
        v-if="isShowIframe"
        allowfullscreen
        allowtransparency
        frameborder="0"
        :src="url"
        ref="iframeId"
    >
    </iframe>
  </div>
</template>

<script scoped>
export default {
  name: 'IframeVideo',
  data: function () {
    return {
      iframeStyle: 'width: 100%',
      isShowIframe: true,
    }
  },
  props: {
    url: String,
  },

  methods: {
    reloadIframe: function () {
      this.isShowIframe = !this.isShowIframe
      window.setTimeout(() => {
        this.isShowIframe = !this.isShowIframe
      }, 0)
    },
  },
}
</script>

<style scoped>
* {
  /*width: calc();*/
}
</style>
